<script>
import Plot from "./Plot.svelte";
export let height = .4*screen.height;
export let width = .4*screen.width;
export let points = [];
let drawing = true;
let mouseDown = false;
let path;
let radius = 2;
let area = [];
$: d = points_to_path();
$: randompoints = [];

$: points_to_path = () => {
    let d = "";
    for (let i in points){
        if (i == 0){
            d += `M${points[i].x} ${points[i].y} `
        } else {
            let p = points[i]
            d += `L${p.x} ${p.y} `
        }
        
    }
    d += " Z";
    return d;
}

function calc_area(){
    area = [];
    for (let i in randompoints){
        i = Number(i);
        let inside = 0;
        for (let j=0; j<=i; j++){
            if (randompoints[j].inside){
                inside += 1;
            }
        }
        console.log(i+1, inside)
        area.push({x:Number(i), y:inside/(i+1)});
    }
    area = area;
}

function handle_click(e){
    if (mouseDown && drawing){
        let svg = document.getElementById('svg')
        var p = svg.createSVGPoint();
        p.x = e.clientX;
        p.y = e.clientY;
        var ctm = svg.getScreenCTM().inverse();
        var p =  p.matrixTransform(ctm);
        points.push(p);
        d = points_to_path();
    }
}

function random_point(n){
    for (let i = 0; i<n; i++){
        var p = svg.createSVGPoint();
        p.x = Math.random()*width;
        p.y = Math.random()*height;
        p.inside = path.isPointInFill(p);
        randompoints.push(p);
        randompoints = randompoints;
    }    
    calc_area();
}

</script>

<style>
    svg {
        outline: solid black;
        margin-bottom: 1em;
    }

    path {
        stroke: green;
        fill: grey;
    }

    .inside {
        fill: red;
    }

    .row{
        display: flex;
    }

    .col{
        flex: 50%;
    }

</style>

<div class="row">
    <div class="col">

        <svg id="svg" height={height} width={width} 
        on:mousedown={() => {mouseDown = true;}} 
        on:mousemove={handle_click} 
        on:mouseup={() => {mouseDown = false;}}
        viewBox="0 0 {width} {height}"
        
        >
        {#if points_to_path() != " Z"}
        <path bind:this={path} id="path" {d} />
        {/if}
        {#each randompoints as rp}
        <circle class={rp.inside?"inside":""} cx={rp.x} cy={rp.y} r={radius}/>
        {/each}
        </svg>
        <br/>
        <button on:click= {() => {drawing = false}} disabled = {!drawing}>Fertig!</button>
        <button on:click = {() => random_point(1)} disabled = {drawing}> +1 Punkt </button>
        <button on:click = {() => random_point(10)} disabled = {drawing}> +10 Punkte </button>
        <button on:click = {() => random_point(100)} disabled = {drawing}> +100 Punkte </button>
    </div>
    
    <div class="col">
        <Plot ndata={area} width={.4*screen.width} height={.4*screen.height}/>
    </div>   
</div>