<!-- 
Svelted version of Exercise 2 of Front End Masters course Introduction to Data Visualization with d3.js v4 by Shirley Wu
https://frontendmasters.com/courses/d3-v4/
-->
<script>
    import { line, curveStep, scaleLinear, timeParse, extent, scaleTime, range, ticks} from 'd3';
	
	let el;
		
	export let width;
	export let height;
	var margin = { top: 20, bottom: 20, left: 20, right: 20 };
	
    export let ndata;
    $: console.log("ndata; ", ndata)
    $: console.log("data; ", data)

    let data = [];

    $: {data = [];
        for (let nd of ndata){
            data.push(nd)
            data = data;
        }
        console.log(data);
    }
    

    $: console.log("Data: ", data);
	// scales
	$: extentX = extent(data, (d) => d.x);
	$: xScale = scaleLinear()
		.domain(extentX)
		.range([margin.left, width - margin.right]);

	$: extentY = extent(data, (d) => d.y);
	$: yScale = scaleLinear()
		.domain(extentY)
		.range([height - margin.bottom, margin.top]);
	
	let path = line()
		.x((d) => xScale(d.x))
        .y((d) => yScale(d.y))
	    .curve(curveStep);
	
    $: xTicks = ticks(extentX[0], extentX[1], 10);
    console.log(xTicks)
	

	// y ticks count to label by 5's
	$: yTicks = ticks(extentY[0], extentY[1], 10);
	
	// d's for axis paths
	$: xPath = `M${margin.left + .5},6V0H${width - margin.right + 1}V6`
    $: yPath = `M-6,${height + .5}H0.5V0.5H-6`


</script>

<style>
	.tick {
		font-size: 11px;
	}
</style>

<svg width={1.1*width} height={1.1*height} bind:this={el} transform="translate({margin.left}, {margin.top})">
	<g>
		<!-- line -->
			<path 
				d="{path(data)}"
				fill="none"
				stroke="blue"
			/>
	</g>
	
	<!-- y axis -->
	<g transform="translate({margin.left}, 0)">
		<path stroke="currentColor" d="{yPath}" fill="none" />

		{#each yTicks as y} 
			<g class="tick" opacity="1" transform="translate(0,{yScale(y)})">
				<line stroke="currentColor" x2="-5" />
				<text dy="0.32em" fill="currentColor" x="-{margin.left}">
					{y}
				</text>
			</g>
         {/each}
         <text fill="currentColor" y="0" dy="0.71em" x="-{margin.left}"> 
            Anteil Fläche 
        </text>

	</g>
	
	<!-- x axis -->
	<g transform="translate(0, {height})">
		<path stroke="currentColor" d="{xPath}" fill="none" />
		
		{#each xTicks as x} 
			<g class="tick" opacity="1" transform="translate({xScale(x)},0)">
				<line stroke="currentColor" y2="6" />
				<text fill="currentColor" y="9" dy="0.71em" x="-{margin.left}">
					{x}
				</text>
            </g>
        {/each}
        <text fill="currentColor" y="20" dy="0.71em" x="{width/2}"> 
            Anzahl Punkte 
        </text>
</svg>


